import { Stack } from "@mui/material";
import React, { useEffect } from "react";
import { Link, Outlet, useLocation } from "react-router-dom";
import PhoneCallbackOutlinedIcon from "@mui/icons-material/PhoneCallbackOutlined";
import CallOutlinedIcon from "@mui/icons-material/CallOutlined";
import HomeOutlinedIcon from "@mui/icons-material/HomeOutlined";
import AddIcCallOutlinedIcon from "@mui/icons-material/AddIcCallOutlined";
import PeopleOutlineOutlinedIcon from "@mui/icons-material/PeopleOutlineOutlined";
import LocalShippingOutlinedIcon from "@mui/icons-material/LocalShippingOutlined";
import ReceiptOutlinedIcon from "@mui/icons-material/ReceiptOutlined";
import LocalPostOfficeOutlinedIcon from "@mui/icons-material/LocalPostOfficeOutlined";
import LogoutOutlinedIcon from "@mui/icons-material/LogoutOutlined";
import "../../style/sidebar/sidebar.css";
import { CookiesProvider } from "react-cookie";
import { Sidebar, SidebarItem } from "react-responsive-sidebar";

const SidebarD = () => {
  const location = useLocation();
  const GetCurrentRoute = () => {
    return location.pathname;
  };
  const activeItem = "activeItem";
  const passiveItem = "passiveItem";

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  return (
    <CookiesProvider className="app">
      <Sidebar
        className="sideBarContainer"
        breakPoint="768"
        background="#FFFFFF"
        toggleIconColor="#7C057B"
        content={[
          <SidebarItem hoverHighlight="#FFFFFF" activeHightlight="#FFFFFF">
            <div className="sidebar">
              {" "}
              <Stack direction="column" spacing={0}>
                <Link to="/homepage" className="sidebarLink">
                  <Stack
                    direction="row"
                    spacing={2}
                    pl={2}
                    className={
                      GetCurrentRoute() === "/" ||
                      GetCurrentRoute() === "/homepage"
                        ? activeItem
                        : passiveItem
                    }
                  >
                    <HomeOutlinedIcon />
                    <p>Home</p>
                  </Stack>
                </Link>
                <Link to="/accept-call" className="sidebarLink">
                  <Stack
                    direction="row"
                    spacing={2}
                    pl={2}
                    className={
                      GetCurrentRoute() === "/" ||
                      GetCurrentRoute() === "/accept-call"
                        ? activeItem
                        : passiveItem
                    }
                  >
                    <PhoneCallbackOutlinedIcon />
                    <p>Geln jalar</p>
                  </Stack>
                </Link>
                <Link to="/ringin-call" className="sidebarLink">
                  <Stack
                    direction="row"
                    spacing={2}
                    pl={2}
                    className={
                      GetCurrentRoute() === "/" ||
                      GetCurrentRoute() === "/ringin-call"
                        ? activeItem
                        : passiveItem
                    }
                  >
                    <CallOutlinedIcon />
                    <p>Kabul edilen jalar</p>
                  </Stack>
                </Link>
                <Link to="/missed-call" className="sidebarLink">
                  <Stack
                    direction="row"
                    spacing={2}
                    pl={2}
                    className={
                      GetCurrentRoute() === "/" ||
                      GetCurrentRoute() === "/missed-call"
                        ? activeItem
                        : passiveItem
                    }
                  >
                    <AddIcCallOutlinedIcon />
                    <p>Gberilen jalar</p>
                  </Stack>
                </Link>
                <Link to="/customer" className="sidebarLink">
                  <Stack
                    direction="row"
                    spacing={2}
                    pl={2}
                    className={
                      GetCurrentRoute() === "/" ||
                      GetCurrentRoute() === "/customer"
                        ? activeItem
                        : passiveItem
                    }
                  >
                    <PeopleOutlineOutlinedIcon />
                    <p>Mderiler</p>
                  </Stack>
                </Link>
                <Link to="/order" className="sidebarLink">
                  <Stack
                    direction="row"
                    spacing={2}
                    pl={2}
                    className={
                      GetCurrentRoute() === "/" ||
                      GetCurrentRoute() === "/order"
                        ? activeItem
                        : passiveItem
                    }
                  >
                    <ReceiptOutlinedIcon />
                    <p>Sargytlar</p>
                  </Stack>
                </Link>
                <Link to="/courier" className="sidebarLink">
                  <Stack
                    direction="row"
                    spacing={2}
                    pl={2}
                    className={
                      GetCurrentRoute() === "/" ||
                      GetCurrentRoute() === "/courier"
                        ? activeItem
                        : passiveItem
                    }
                  >
                    <LocalShippingOutlinedIcon />
                    <p>Eltip berijiler</p>
                  </Stack>
                </Link>
                <Link to="/inbox" className="sidebarLink">
                  <Stack
                    direction="row"
                    spacing={2}
                    pl={2}
                    className={
                      GetCurrentRoute() === "/" ||
                      GetCurrentRoute() === "/inbox"
                        ? activeItem
                        : passiveItem
                    }
                  >
                    <LocalPostOfficeOutlinedIcon />
                    <p>Geln hatlar</p>
                  </Stack>
                </Link>
                <Link to="/login" className="sidebarLink">
                  <Stack
                    direction="row"
                    spacing={2}
                    pl={2}
                    className={
                      GetCurrentRoute() === "/" ||
                      GetCurrentRoute() === "/login"
                        ? activeItem
                        : passiveItem
                    }
                  >
                    <LogoutOutlinedIcon />
                    <p>Log out</p>
                  </Stack>
                </Link>
              </Stack>
            </div>
          </SidebarItem>,
        ]}
      >
        <Outlet />
      </Sidebar>
    </CookiesProvider>
  );
};

export default SidebarD;
