import React from "react";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import { Stack } from "@mui/material";
import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import "../../style/inbox/inbox.css";
import Search from "../common-view/Search";

const style = {
  position: "absolute",
  top: "20%",
  left: "85%",
  transform: "translate(-50%, -50%)",
  width: 150,
  bgcolor: "background.paper",
  //   border: "1px solid red",
  //   boxShadow: 24,
  borderRadius: "16px",
  borderColor: "#5E9CCE",
  p: 1,
};

const InboxCard = () => {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  return (
    <div className="inboxCard">
      <Search />
      <div className="inboxCardContainer">
        <Stack spacing={3} pt={3}>
          <div className="inboxFirstRow">
            <label>#123 / Message tite</label>
            <MoreVertIcon onClick={handleOpen} style={{ cursor: "pointer" }} />
          </div>
          <div className="inboxSecondRow">
            <Stack direction={"row"} spacing={15}>
              <label>Gayypow Halil / courier</label>
              <label>28.06.2022</label>
              <label>19:00</label>
            </Stack>
          </div>
          <div className="inboxThirdRow">
            <label>Some message description here...</label>
          </div>
        </Stack>
      </div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h6">
            Delete
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            Mark as read
          </Typography>
        </Box>
      </Modal>
    </div>
  );
};

export default InboxCard;
