import { Grid, Stack } from "@mui/material";
import React from "react";
import styled from "styled-components";
import EditIcon from "@mui/icons-material/Edit";
import Search from "../common-view/Search";

const courierData = [
  {
    id: 1,
    number: "+99363430338",
    name: "Amanov Aman Amanovich",
    status: "active",
    order: "10 gezek",
    carNumber: "5677",
    orderStatus: "Free/On the way",
  },
  {
    id: 2,
    number: "+99363430338",
    name: "Amanov Aman Amanovich",
    status: "active",
    order: "10 gezek",
    carNumber: "5677",
    orderStatus: "Free/On the way",
  },
  {
    id: 3,
    number: "+99363430338",
    name: "Amanov Aman Amanovich",
    status: "active",
    order: "10 gezek",
    carNumber: "5677",
    orderStatus: "Free/On the way",
  },
  {
    id: 4,
    number: "+99363430338",
    name: "Amanov Aman Amanovich",
    status: "active",
    order: "10 gezek",
    carNumber: "5677",
    orderStatus: "Free/On the way",
  },
];

const CourierCard = () => {
  return (
    <CourierCardSection>
      <Search />
      <Grid container>
        {courierData.map((item, i) => {
          return (
            <Grid item lg={6} md={6} xs={12} sm={12} mt={2}>
              <CouCard>
                <Stack>
                  <Stack direction="row" justifyContent={"space-between"}>
                    <Number>{item.number}</Number>
                    <EditIcon style={{ color: "#5e9cce", cursor: "pointer" }} />
                  </Stack>
                  <Name>{item.name}</Name>
                  <CourierCardInfo>
                    <CourierCardInfoFirst>
                      <Stack direction="row" spacing={3}>
                        <h4>Status: </h4>
                        <h4>{item.status}</h4>
                      </Stack>
                    </CourierCardInfoFirst>
                    <CourierCardInfoSecond>
                      <Stack direction="row" alignItems="center" spacing={2}>
                        <h4>Sargytlar:</h4>
                        <h4
                          style={{
                            fontSize: "12px",
                            color: "lightblue",
                            textDecoration: "underline",
                          }}
                        >
                          {item.order}
                        </h4>
                      </Stack>
                    </CourierCardInfoSecond>
                    <CourierCardInfoThird>
                      <Stack direction="row" spacing={3}>
                        <h4>Car number:</h4>
                        <h4>{item.carNumber}</h4>
                      </Stack>
                    </CourierCardInfoThird>
                    <CourierCardInfoFourth>
                      <Stack direction="row" spacing={3}>
                        <h4>Order status:</h4>
                        <h4>{item.orderStatus}</h4>
                      </Stack>
                    </CourierCardInfoFourth>
                  </CourierCardInfo>
                </Stack>
              </CouCard>
            </Grid>
          );
        })}
      </Grid>
    </CourierCardSection>
  );
};

const CourierCardSection = styled.div`
  width: 87%;
  margin-left: 50px;
`;
const CouCard = styled.div`
  width: 95%;
  height: auto;
  border-radius: 16px;
  padding-right: 500px;
  background: #fff;
  padding: 12px;
`;
const Name = styled.h3``;

const Number = styled.h4``;

const CourierCardInfo = styled.div`
  width: 100%;
  border-radius: 16px;
  margin-top: 15px;
  margin-bottom: 20px;
`;

const CourierCardInfoFirst = styled.div`
  background: lightblue;
  width: 100%;
  padding: 15px;
  border-radius: 16px 16px 0px 0px;
`;
const CourierCardInfoSecond = styled.div`
  width: 100%;
  background: lightgray;
  padding: 15px;
  display: flex;
  align-items: center;
`;
const CourierCardInfoThird = styled.div`
  width: 100%;
  background: lightblue;
  padding: 15px;
`;
const CourierCardInfoFourth = styled.div`
  width: 100%;
  background: lightgray;
  padding: 15px;
  border-radius: 0px 0px 16px 16px;
`;
export default CourierCard;
